.comments_sub_form_comments {
    display: flex;
    width: 70%;
}
.comments_ibox {
    width: 80%;
    padding: 0.5%;
    padding-left: 2%;
    background-color: transparent;
    color: wheat;
    border: 0;
    border-bottom: 2px solid grey;
}
.comment_add_btn_comment {
    cursor: pointer;
    border: 2px solid transparent;
    border-bottom: 2px solid rgb(64, 64, 64);
    background-color: transparent;
    color: aliceblue;
    font-size: large;
    width: 20%;
}
.comment_add_btn_comment:hover{
    border: 2px solid rgb(64, 64, 64, 0.05);
    border-bottom: 2px solid rgb(64, 64, 64);
    background-color: rgba(240, 248, 255, 0.05);
}
.display_comment_container {
   padding: 1rem; 
}
/* display comments */
.comment_body {
    color: rgb(197, 197, 197);
    margin: 0;
    margin-top: 0.5rem;
}
.usercommented {
    color: rgb(50, 50, 50);
    margin: 0;
}
.EditDel_DisplayComment {
    padding: 0.2rem;
    margin: 0;
    margin-bottom: 0.8rem;
    color: rgb(44, 85, 209, 0.696);
    cursor: pointer;
}
.EditDel_DisplayComment i {
    padding: 0.2rem;
}
